.HelpChildren{
  display: none;
  margin-bottom: 50px;
}
@media screen and (min-width: 700px) {
  .HelpChildren{
    display: block;
    background: #fff;
    padding: 45px 20px;
    border-radius: 20px;
    background:#fff url('../img/children.png')no-repeat bottom right;
  }

  .HelpChildren__title{
    color: #00C0FF;
    font-size: 43px;
    text-transform: uppercase;
    font-family: 'PollywogCry';
    padding-bottom: 30px;
  }

  .HelpChildren__text {
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.4);
    color: #111;
    width: 408px;
    line-height: 24px;
    font-size: 18px;
    margin-bottom: 40px;
    backdrop-filter:blur(3px);
    box-shadow: 0px 0px 20px rgba(255,255,255, 0.2);
  }
  .HelpChildren__text > strong {
    color: #00C0FF;
    font-weight: 700;
    text-transform: uppercase;
  }

  .HelpChildren__btn {
    display: inline-block;
    text-transform: uppercase;
    color: #fff;
    text-decoration: none;
    background-color: #00c0ff;
    padding: 15px 40px;
    border-radius: 20px;
    transition: 0.2s;
  }
  .HelpChildren__btn:hover {
    background-color:#0094FF
  }
}